import { Card, CardContent, Typography } from "@mui/material";
import { Planet } from "../../../types/planets";
import { getClimateColor } from "@/utils/planets";

export const PlanetCard = ({ result }: { result: Planet }) => {
  const primaryClimate = result.climate.split(",")[0].trim();
  const climateColor = getClimateColor(primaryClimate);
  const getClimateBackground = (color: string) => {
    return `
            radial-gradient(circle at 30% 20%, ${color}08 0%, transparent 60%),
            radial-gradient(circle at 70% 80%, ${color}05 0%, transparent 60%),
            linear-gradient(135deg, #f8f9fa 0%, #e9ecef 50%, #f1f3f4 100%)
          `;
  };
  return (
    <Card
      key={result.name}
      sx={{
        background: getClimateBackground(climateColor),
        border: `1px solid ${climateColor}30`,
        borderRadius: "16px",
        position: "relative",
        transition: "all 0.4s ease",
        minHeight: "300px",
        "&:hover": {
          cursor: "pointer",
          boxShadow: `
                  0 10px 20px ${climateColor}20,
                  0 4px 8px rgba(0,0,0,0.1),
                  inset 0 1px 0 rgba(255,255,255,0.2)
                `,
          borderColor: climateColor,
        },
        "&::before": {
          content: '""',
          position: "absolute",
          top: 0,
          left: 0,
          right: 0,
          height: "2px",
          background: `linear-gradient(90deg, transparent, ${climateColor}, transparent)`,
        },
      }}
    >
      <CardContent>
        <Typography variant="h6">{result.name}</Typography>
      </CardContent>
    </Card>
  );
};
